import React from 'react';
import CodePanel from '../../Components/CodePanel';

export default () => {
  return (
    <>
      <section className="accent-bottom panel">
        <h2>Non-abundant sums</h2>
        <h4>Problem 23</h4>
        <p>
          A perfect number is a number for which the sum of its proper divisors
          is exactly equal to the number. For example, the sum of the proper
          divisors of 28 would be 1 + 2 + 4 + 7 + 14 = 28, which means that 28
          is a perfect number.
        </p>
        <p>
          A number n is called deficient if the sum of its proper divisors is
          less than n and it is called abundant if this sum exceeds n.
        </p>
        <p>
          As 12 is the smallest abundant number, 1 + 2 + 3 + 4 + 6 = 16, the
          smallest number that can be written as the sum of two abundant numbers
          is 24. By mathematical analysis, it can be shown that all integers
          greater than 28123 can be written as the sum of two abundant numbers.
          However, this upper limit cannot be reduced any further by analysis
          even though it is known that the greatest number that cannot be
          expressed as the sum of two abundant numbers is less than this limit.
        </p>
        <p>
          Find the sum of all the positive integers which cannot be written as
          the sum of two abundant numbers.
        </p>
      </section>
      <CodePanel title="JavaScript">{`
        () => {
          let
          limit = 28123,
          abundants = [],
          sieve = new Array(limit).fill(true),
          sumsOfDivisors = new Array(limit).fill(1);

          for (let i = 2; i <= limit; i++) {
            for (let j = i + i; j < limit; j += i) {
              sumsOfDivisors[j] += i;
            }
            if (sumsOfDivisors[i] > i && sumsOfDivisors[i] < limit) {
              abundants.push(i);
              abundants.map(abundant => {
                sieve[abundant + i] = false;
              });
            }
          }
          return sieve.reduce((sum, value, key) => value
            ? sum + key
            : sum
          , 0);
        }
    `}</CodePanel>
    </>
  );
};